.safety {
    background: #FFFFFF;
    width: 300px;
    padding: 35px 32px 28px 30px;
    border: 1px solid rgba(71, 71, 128, 0.12);

    &-Wrap {
        padding-bottom: 115px;
        width: 100%;
        max-width: 1000px;
        display: flex;
        flex-wrap: wrap;
        background: #FFFFFF;
    }

    &-title {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 800;
        font-size: 18px;
        line-height: 22px;
        color: #171A4A;
        width: 100%;
        max-width: 183px;
    }

    &-text {
        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 12px;
        line-height: 16px;
        color: #171A4A;
        opacity: 0.6;
        padding-top: 10px;
        padding-bottom: 31px;
    }


}

#blue {
    background: linear-gradient(217.37deg, #700BD5 17.74%, #111ABF 99.42%), linear-gradient(130.09deg, #0BD5D2 -61.14%, #3B42C5 71.33%);

    .safety-title {
        color: #FFFFFF;
    }

    .safety-text {
        color: #FFFFFF;
    }
}